<template>
  <div class="main-content">
    <!-- 列表页 -->
    <div v-if="showFlag">
      <el-form :inline="true" :model="searchForm" class="form-content">
        <el-row
          :gutter="20"
          class="slt"
          :style="{
            justifyContent:
              contents.searchBoxPosition == '1'
                ? 'flex-start'
                : contents.searchBoxPosition == '2'
                ? 'center'
                : 'flex-end',
          }"
        >
          <el-form-item :label="contents.inputTitle == 1 ? '绘本名称' : ''">
            <el-input
              v-if="contents.inputIcon == 1 && contents.inputIconPosition == 1"
              prefix-icon="el-icon-search"
              v-model="searchForm.huibenmingcheng"
              placeholder="绘本名称"
              clearable
            ></el-input>
            <el-input
              v-if="contents.inputIcon == 1 && contents.inputIconPosition == 2"
              suffix-icon="el-icon-search"
              v-model="searchForm.huibenmingcheng"
              placeholder="绘本名称"
              clearable
            ></el-input>
            <el-input
              v-if="contents.inputIcon == 0"
              v-model="searchForm.huibenmingcheng"
              placeholder="绘本名称"
              clearable
            ></el-input>
          </el-form-item>
          <el-form-item>
            <el-button
              v-if="
                contents.searchBtnIcon == 1 &&
                contents.searchBtnIconPosition == 1
              "
              icon="el-icon-search"
              type="success"
              @click="search()"
              >{{ contents.searchBtnFont == 1 ? "查询" : "" }}</el-button
            >
            <el-button
              v-if="
                contents.searchBtnIcon == 1 &&
                contents.searchBtnIconPosition == 2
              "
              type="success"
              @click="search()"
              >{{ contents.searchBtnFont == 1 ? "查询" : ""
              }}<i class="el-icon-search el-icon--right"
            /></el-button>
            <el-button
              v-if="contents.searchBtnIcon == 0"
              type="success"
              @click="search()"
              >{{ contents.searchBtnFont == 1 ? "查询" : "" }}</el-button
            >
          </el-form-item>
        </el-row>

        <el-row
          class="ad"
          :style="{
            justifyContent:
              contents.btnAdAllBoxPosition == '1'
                ? 'flex-start'
                : contents.btnAdAllBoxPosition == '2'
                ? 'center'
                : 'flex-end',
          }"
        >
          <el-form-item>
            <el-button
              v-if="
                isAuth('yuedudaka', '新增') &&
                contents.btnAdAllIcon == 1 &&
                contents.btnAdAllIconPosition == 1
              "
              type="success"
              icon="el-icon-plus"
              @click="addOrUpdateHandler()"
              >{{ contents.btnAdAllFont == 1 ? "新增" : "" }}</el-button
            >
            <el-button
              v-if="
                isAuth('yuedudaka', '新增') &&
                contents.btnAdAllIcon == 1 &&
                contents.btnAdAllIconPosition == 2
              "
              type="success"
              @click="addOrUpdateHandler()"
              >{{ contents.btnAdAllFont == 1 ? "新增" : ""
              }}<i class="el-icon-plus el-icon--right"
            /></el-button>
            <el-button
              v-if="isAuth('yuedudaka', '新增') && contents.btnAdAllIcon == 0"
              type="success"
              @click="addOrUpdateHandler()"
              >{{ contents.btnAdAllFont == 1 ? "新增" : "" }}</el-button
            >
            <el-button
              v-if="
                isAuth('yuedudaka', '删除') &&
                contents.btnAdAllIcon == 1 &&
                contents.btnAdAllIconPosition == 1 &&
                contents.tableSelection
              "
              :disabled="dataListSelections.length <= 0"
              type="danger"
              icon="el-icon-delete"
              @click="deleteHandler()"
              >{{ contents.btnAdAllFont == 1 ? "删除" : "" }}</el-button
            >
            <el-button
