        tableBtnIconPosition: "1",
        searchBtnHeight: "46px",
        tableBgColor: "rgba(255, 251, 251, 1)",
        inputIconColor: "rgba(0, 0, 0, 1)",
        searchBtnBorderRadius: "50px",
        tableStripe: false,
        btnAdAllWarnFontColor: "rgba(0, 0, 0, 1)",
        tableBtnDelBgColor: "rgba(255, 255, 255, 1)",
        searchBtnIcon: "1",
        tableSize: "medium",
        searchBtnBorderStyle: "solid",
        text: {
          padding: "0",
          boxShadow: "0 0 0px rgba(0,0,0,.1)",
          margin: "0 auto",
          borderColor: "rgba(0,0,0,.3)",
          backgroundColor: "rgba(144, 238, 144, 0)",
          color: "#333",
          borderRadius: "0",
          borderWidth: "0",
          width: "auto",
          lineHeight: "60px",
          fontSize: "30px",
          borderStyle: "solid",
        },
        tableSelection: true,
        searchBtnBorderWidth: "8px",
        tableContentFontSize: "14px",
        searchBtnBgColor: "rgba(220, 86, 101, 1)",
        inputTitleSize: "15px",
        btnAdAllBorderColor: "rgba(255, 255, 255, 1)",
        pageJumper: true,
        btnAdAllIconPosition: "1",
        searchBoxPosition: "2",
        tableBtnDetailFontColor: "rgba(28, 2, 5, 1)",
        tableBtnHeight: "45px",
        pagePager: true,
        searchBtnBorderColor: "rgba(255, 255, 255, 1)",
        tableHeaderFontColor: "rgba(0, 0, 0, 1)",
        inputTitle: "1",
        tableBtnBorderRadius: "30px",
        btnAdAllFont: "1",
        btnAdAllDelFontColor: "rgba(0, 0, 0, 1)",
        tableBtnIcon: "1",
        btnAdAllHeight: "45px",
        btnAdAllWarnBgColor: "rgba(255, 255, 255, 0)",
        btnAdAllBorderWidth: "0",
        tableStripeFontColor: "rgba(0, 0, 0, 1)",
        tableBtnBorderStyle: "solid",
        inputHeight: "46px",
        btnAdAllBorderRadius: "0",
        btnAdAllDelBgColor: "rgba(255, 255, 255, 0)",
        pagePrevNext: true,
        btnAdAllAddBgColor: "rgba(255, 255, 255, 0)",
        searchBtnFont: "1",
        tableIndex: true,
        btnAdAllIcon: "1",
        tableSortable: false,
        pageSizes: true,
        tableFit: true,
        pageBtnBG: false,
        searchBtnFontSize: "15px",
        tableBtnEditBgColor: "rgba(255, 255, 255, 1)",
        box: {
          padding: "10px 10px 0px 0px",
          boxShadow: "0 0 6px rgba(0,0,0,0)",
          flag: "2",
          backgroundImage:
            "http://codegen.caihongy.cn/20211126/db5b4b156c624180af3c5f353684fe73.jpg",
          background: "",
        },
        inputBorderWidth: "8px",
        inputFontPosition: "2",
        inputFontColor: "rgba(0, 0, 0, 1)",
        pageEachNum: 10,
        tableHeaderBgColor: "rgba(220, 86, 101, 1)",
        inputTitleColor: "#333",
        btnAdAllBoxPosition: "1",
        tableBtnDetailBgColor: "rgba(255, 255, 255, 1)",
        inputIcon: "0",
        searchBtnIconPosition: "1",
        btnAdAllFontSize: "18px",
        inputBorderStyle: "solid",
        tableHoverFontColor: "#333",
